import styled from'styled-components';

export const HomeSection=styled.div`
    height: 500px;
    background: url('images/home-bg.jpg');
    background-size: cover;
    background-position: center;
    text-align: center;
    position: relative;
`;
export const HomeInfo=styled.div`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%)
`;

export const HomeH2=styled.h2`
    font-size: 60px;
    font-weight: bold;
    color: #5e5e5e;
    @media(max-width:768px){
        font-size: 40px;
    }
    @media (max-width:575px){
        font-size: 30px;
    }
`;

export const HomeH4=styled.h4`
    font-size: 35px;
    color: #eb5424;
    margin-bottom: 20px;
    @media(max-width:768px){
        font-size: 20px
    }
`;

export const HomeP=styled.p`
    font-size: 20px;
    line-height: 1.5;
    color: #888;
    margin-bottom: 20px;
    @media(max-width:768px){
        font-size: 15px
    }
`;

export const HomeSpan=styled.span`
    color: #000
`;
export const HomeBtn=styled.button`
    background: #eb5424;
    color: #fff;
    font-size: 18px;
    width: 150px;
    border: 0;
    padding: 15px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.5s ease-in-out;
    &:hover{
        background: #fff;
        color: #eb5424
    }
`;

